:root {
  --body-font: "Inconsolata", monospace;
  --size-8: 8px;
  --size-16: 16px;
  --size-24: 24px;
}

@font-face {
  font-family: Inconsolata;
  src: url("./assets/Inconsolata-VariableFont_wdth,wght.ttf") format("truetype");
}

html, body {
  font-family: var(--body-font);
  font-size: 16px;
  font-weight: 400;
}
